#color-picker{ 
    width: min-content;
    margin: auto;
}

#canvases-container{
    display: flex;
    margin-bottom: 16px;
}

#saturationCircle{
    position: absolute;
    display: inline; 
    margin-left: -5.5px; 
    width: 24px; 
    height: 7px;
    border: 1px solid; 
    border-radius: 3px;
}

#pickerCircle{
    width: 16px;
    height: 16px; 
    border: 1px solid; 
    border-radius: 15px;
    position: absolute; 
    z-index: 100;
}

#main-canvas-color-picker{
    display: inline; 
    position: relative; 
    float: left;
    border: solid 1px #0000001c;
    border-radius: 2%;
}

#saturation-canvas-color-picker{ 
    display: inline; 
    margin-right: 7px; 
    border: 1px solid #394B59;
    border-radius: 2%;
}

.input-boxes{ 
    display: inline-block; 
    vertical-align: top; 
    margin-right: 26px; 
    
}
.input-boxes div label{ 
    margin-bottom: 0
}
.input-boxes > div{ 
    display: flex;  
    margin-bottom: 0.5em;
    align-items: center;
    vertical-align: middle;
}

#picked-color{ 
    padding: 1.8em 4.8em; 
    display: inline-block; 
    border-radius: 1%; 
    margin-bottom: 16px; 
}

#hex-intput{ 
    display: inline-block; 
}
#hex-intput-txt-box{ 
   width: 82px;
   margin-left: 2px; 
   height: 1.4em;
}

#color-inputs-container{
    display: flex;
    justify-content: space-between;
}

