.slider-label { 
}

.single-tile-basic {
    display: inline-block;
    cursor: pointer;
    transition: border-radius 0.1s linear;
}
.single-tile-basic:hover{
    border-radius: 25%
}
.selected-tile, .selected-tile:hover {
    border-radius:50%
}
.single-tile-basic:last-child {
    border-right: none
}

.level-1-holder { 
    border-collapse: collapse;
}
.level-1-holder div{
    border: 1px solid white;
} 
.tiles-holder{
    /*line-height: 0;*/
    OVERFLOW: auto;
    height: 62%;
}

