.App {
  height: -webkit-fill-available;
  grid-template-rows: 5% 95%;
  display: grid; }

#root {
  height: 100%; }

h1, h2, h3, h4, h5, h6 {
  color: #212121;
  font-weight: 400 !important; }

html {
  font-family: Roboto,serif; }

body {
  height: 100%;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1;
  font-weight: 400 !important;
  color: #212121 !important;
  font-family: 'Roboto', sans-serif;
  font-size: 0.83333em; }

footer {
  background: #3b3b3b; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.header {
  justify-content: center;
  display: flex;
  flex-direction: column;
  padding-left: 24px;
  background: #3b3b3b; }

.header > h2 {
  margin: 0;
  color: #d4d4d4;
  font-weight: 400;
  letter-spacing: 0.05em; }

.filters-part {
  display: grid;
  border-top: 1px solid #d4d4d4;
  grid-template-rows: 13% 87%; }

.filters-long-part {
  display: grid;
  grid-template-rows: 15% 85%;
  height: 100%; }

.selected-mode {
  background: white !important; }

.select-mode-table > div {
  justify-content: center;
  display: flex;
  flex-direction: column;
  -webkit-flex: 1;
  flex: 1;
  text-align: center;
  font-size: 1em;
  color: #3b3b3b;
  background: #ededed;
  cursor: pointer;
  transition: box-shadow;
  transition-timing-function: cubic-bezier(0.4, 0, 0.6, 1);
  font-weight: 600; }

.select-mode-table > div:hover {
  background: white; }

.select-mode-table > div:nth-last-child(1) {
  border: none; }

.select-mode-table {
  display: flex; }

.filters-short {
  width: 62%;
  margin: auto; }

#hsl-basic-filters {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 62%;
  margin-left: 32px;
  justify-content: space-around; }

#hsl-basic-filters > div {
  font-size: 1em;
  min-height: 60px; }

.base-color-select-container:hover,
.base-color-select-container:focus {
  box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2), 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12);
  cursor: pointer; }

.base-color-select-container {
  width: 62%;
  margin: auto;
  margin-left: 32px;
  box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12);
  font-weight: 600;
  padding: 8px 0; }

.select-color-btn {
  justify-content: center;
  display: flex;
  flex-direction: column;
  height: 100%;
  text-align: center;
  margin: auto;
  font-size: 0.83333em;
  font-weight: 500; }

#palette-container {
  border-right: 1px solid #d4d4d4;
  border-top: 1px solid #d4d4d4;
  display: grid;
  grid-template-columns: 19% 62% 19%;
  overflow: auto; }

.tiles-actions-bar {
  color: #878787 !important;
  padding-top: 16px; }

.tiles-actions-bar2 > div {
  justify-content: center;
  display: flex;
  flex-direction: column; }

.arrow-btn {
  padding-top: 24px;
  margin-left: 16px;
  cursor: pointer;
  display: block; }

.arrow-btn:hover {
  color: #6e6e6e !important; }

.selected-color-panel {
  width: fit-content;
  height: fit-content;
  padding: 8px 16px;
  text-align: center;
  display: grid;
  font-size: 1em;
  border-radius: 2px;
  cursor: pointer;
  float: right;
  border-radius: 4px;
  margin-right: 24px; }

.horizontal-holder-sl {
  display: grid;
  grid-template-columns: 38% 62%;
  height: 100%; }

.horizontal-holder-ls {
  display: grid;
  grid-template-columns: 62% 38%;
  height: 100%; }

.vertical-holder-sl {
  height: 100%;
  display: grid;
  grid-template-rows: 38% 62%; }

.vertical-holder-ls {
  height: 100%;
  display: grid;
  grid-template-rows: 62% 38%; }

/* SINGLE COMPONENTS */
.export-json-btn {
  margin-left: 16px;
  font-size: 1em;
  cursor: pointer; }

.export-json-btn:hover {
  color: #6e6e6e !important; }

.ant-slider-rail {
  background-color: #ededed; }

.ant-slider-track {
  background: transparent; }

.ant-slider-step {
  border: none;
  border-radius: 50%; }

.ant-slider-handle:hover {
  background-color: #394b59; }

.ant-slider-handle:hover {
  box-shadow: 0 0 0 1px rgba(16, 22, 26, 0.4);
  background-color: #30404d; }

.ant-slider-handle-white {
  box-shadow: inset 0 0 0 1px rgba(16, 22, 26, 0.2), inset 0 -1px 0 rgba(16, 22, 26, 0.1);
  background-color: #f5f8fa;
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0.8)), to(rgba(255, 255, 255, 0)));
  background-image: linear-gradient(180deg, rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0));
  color: #182026; }

.ant-slider-handle:hover {
  box-shadow: inset 0 0 0 1px rgba(16, 22, 26, 0.2), inset 0 -1px 0 rgba(16, 22, 26, 0.1);
  background-clip: padding-box;
  background-color: #ebf1f5;
  z-index: 2;
  box-shadow: 0 0 0 1px rgba(16, 22, 26, 0.2), 0 1px 1px rgba(16, 22, 26, 0.2);
  cursor: -webkit-grab;
  cursor: grab; }

.ant-slider-handle {
  background-color: white;
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0.05)), to(rgba(255, 255, 255, 0)));
  background-image: linear-gradient(180deg, rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0));
  color: #f5f8fa;
  position: absolute;
  left: 0;
  border-radius: 3px;
  box-shadow: 0 0 0 1px rgba(16, 22, 26, 0.2), 0 1px 1px rgba(16, 22, 26, 0.2);
  cursor: pointer;
  width: 16px;
  height: 16px;
  border: none; }

.ant-slider-handle:focus {
  border: none;
  box-shadow: 0 0 0 1px rgba(16, 22, 26, 0.2), 0 1px 1px rgba(16, 22, 26, 0.2); }

.ant-slider {
  margin-top: 0;
  margin-bottom: 0; }

.ant-slider-dot-active {
  box-shadow: 0 0 0 1px rgba(16, 22, 26, 0.2), 0 1px 1px rgba(16, 22, 26, 0.2);
  border: none; }

.ant-slider-dot {
  box-shadow: 0 0 0 1px rgba(16, 22, 26, 0.2), 0 1px 1px rgba(16, 22, 26, 0.2);
  border: none;
  background-color: #f9fafb;
  background-image: linear-gradient(180deg, rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0)); }

.ant-slider-mark-text-active {
  /* color: rgb(249, 250, 251)*/ }

.ant-slider-mark-text {
  color: #212121;
  font-size: 0.69444em; }
