

/* FILTERS */
#number-of-colors-input{
}
.hsl-advanced-filters{
    padding-bottom: 24px;
    justify-content: space-between;
    display: flex;
}


/* PRESETS */
#presets-group {
    display: inline-flex;
}
#select-preset-style {
    float: right;
    width: 120;
}

/* TILES */
.generated-tiles{
   /* line-height: 1.45; */
    width: 100%;
    overflow-y: auto;
    background: white;
    align-items: baseline;
    display: flex; 
    justify-content: center;
    flex-wrap: wrap; 
    justify-items: flex-start;
    align-content: baseline;
}
.tiles-row {
    padding: 8px 0;
    display: flex; 
    justify-content: center;
    flex-wrap: wrap; 
    justify-items: flex-start;
} 
.single-tile {
    padding: 0.5cm;
    cursor: pointer;
    transition: border-radius 0.2s ease-in;
}
.single-tile:hover{
    border-radius: 25%
}
.selected-tile {
    border-radius:50%
}

.steps .bp3-label.slider-label{
    text-align: right;
}

/* SLIDER */
.hsl-slider{
    margin-bottom: 16px;
}
.slider-input { 
    width: 140px; 
    display: inline-block; 
    vertical-align: bottom;
}
.slider-input-txt { 
    width: 40px; 
    border: none; 
    background-color: transparent;
    margin-left: 6px;
    font-weight: bold;
}
label.bp3-label.slider-label { 
    margin: 0;
    margin-bottom: 16px;
}
